import React from 'react';
import { User, BookOpen, Mic, GraduationCap, FileText, Newspaper, Mail } from 'lucide-react';
import useErrorHandler from '../hooks/useErrorHandler';
import ErrorBoundary from './ErrorBoundary';

interface ProfileSidebarProps {
  activeSection: string;
  setActiveSection: (section: string) => void;
  isMobileMenuOpen: boolean;
  closeMobileMenu: () => void;
}

const ProfileSidebarContent: React.FC<ProfileSidebarProps> = ({ 
  activeSection, 
  setActiveSection, 
  isMobileMenuOpen, 
  closeMobileMenu 
}) => {
  const { handleError } = useErrorHandler();

  const navigationItems = [
    { id: 'publications', label: 'Publications', icon: BookOpen },
    { id: 'talks', label: 'Talks', icon: Mic },
    { id: 'teaching', label: 'Teaching', icon: GraduationCap },
    { id: 'cv', label: 'CV', icon: FileText },
    { id: 'news', label: 'News', icon: Newspaper },
    { id: 'contact', label: 'Contact', icon: Mail },
  ];

  const handleNavClick = (sectionId: string) => {
    try {
      setActiveSection(sectionId);
      closeMobileMenu();
    } catch (error) {
      handleError(error);
    }
  };

  return (
    <>
      {/* Desktop Sidebar */}
      <aside className="fixed left-0 top-16 bottom-0 w-80 bg-white border-r border-gray-200 overflow-y-auto lg:block hidden">
        <div className="p-6">
          {/* Profile Section */}
          <div className="text-center mb-8">
            <div className="w-32 h-32 mx-auto mb-4 bg-gradient-to-br from-blue-500 to-blue-600 rounded-full flex items-center justify-center">
              <User className="w-16 h-16 text-white" />
            </div>
            <h2 className="text-2xl font-bold text-gray-900 mb-2">Dr. Arjun Dave</h2>
            <p className="text-gray-600 mb-4">PhD in Medicine and Health Sciences<br />PhD Student at Norwegian University of Science and Technology</p>
            <p className="text-sm text-gray-500 leading-relaxed">
              Researcher specializing in machine learning, artificial intelligence, and computational linguistics.
            </p>
          </div>

          {/* Navigation */}
          <nav className="space-y-2">
            {navigationItems.map((item) => {
              const Icon = item.icon;
              return (
                <button
                  key={item.id}
                  onClick={() => handleNavClick(item.id)}
                  className={`w-full flex items-center space-x-3 px-4 py-3 text-left rounded-lg transition-all duration-200 ${
                    activeSection === item.id
                      ? 'bg-blue-50 text-blue-700 border-l-4 border-blue-700'
                      : 'text-gray-700 hover:bg-gray-50 hover:text-blue-600'
                  }`}
                >
                  <Icon className="w-5 h-5" />
                  <span className="font-medium">{item.label}</span>
                </button>
              );
            })}
          </nav>
        </div>
      </aside>

      {/* Mobile Sidebar */}
      <aside className={`fixed left-0 top-16 bottom-0 w-80 bg-white border-r border-gray-200 overflow-y-auto z-50 transform transition-transform duration-300 ease-in-out lg:hidden ${
        isMobileMenuOpen ? 'translate-x-0' : '-translate-x-full'
      }`}>
        <div className="p-6">
          {/* Profile Section */}
          <div className="text-center mb-8">
            <div className="w-32 h-32 mx-auto mb-4 bg-gradient-to-br from-blue-500 to-blue-600 rounded-full flex items-center justify-center">
              <User className="w-16 h-16 text-white" />
            </div>
            <h2 className="text-2xl font-bold text-gray-900 mb-2">Dr. Jane Smith</h2>
            <p className="text-gray-600 mb-4">PhD in Computer Science<br />University Research Institute</p>
            <p className="text-sm text-gray-500 leading-relaxed">
              Researcher specializing in machine learning, artificial intelligence, and computational linguistics.
            </p>
          </div>

          {/* Navigation */}
          <nav className="space-y-2">
            {navigationItems.map((item) => {
              const Icon = item.icon;
              return (
                <button
                  key={item.id}
                  onClick={() => handleNavClick(item.id)}
                  className={`w-full flex items-center space-x-3 px-4 py-3 text-left rounded-lg transition-all duration-200 ${
                    activeSection === item.id
                      ? 'bg-blue-50 text-blue-700 border-l-4 border-blue-700'
                      : 'text-gray-700 hover:bg-gray-50 hover:text-blue-600'
                  }`}
                >
                  <Icon className="w-5 h-5" />
                  <span className="font-medium">{item.label}</span>
                </button>
              );
            })}
          </nav>
        </div>
      </aside>
    </>
  );
};

const ProfileSidebar: React.FC<ProfileSidebarProps> = (props) => (
  <ErrorBoundary>
    <ProfileSidebarContent {...props} />
  </ErrorBoundary>
);

export default ProfileSidebar;
