import React from 'react';
import { ExternalLink, FileText } from 'lucide-react';
import useErrorHandler from '../hooks/useErrorHandler';
import ErrorBoundary from './ErrorBoundary';

const PublicationsContent = () => {
  const { handleError } = useErrorHandler();

  const handleLinkClick = async (url: string) => {
    try {
      window.open(url, '_blank');
    } catch (error) {
      handleError(error);
    }
  };

  const publications = [
    {
      year: 2024,
      title: "Advanced Machine Learning Techniques for Natural Language Processing",
      authors: "Smith, J., Johnson, A., Brown, M.",
      venue: "Journal of Artificial Intelligence Research, Vol. 45, pp. 123-156",
      links: [
        { label: "PDF", url: "#" },
        { label: "DOI", url: "#" }
      ]
    },
    {
      year: 2024,
      title: "Computational Approaches to Linguistic Analysis",
      authors: "Smith, J., Davis, R.",
      venue: "Proceedings of ACL 2024, pp. 1245-1252",
      links: [
        { label: "PDF", url: "#" },
        { label: "Code", url: "#" }
      ]
    },
    {
      year: 2023,
      title: "Deep Learning Models for Text Classification",
      authors: "Johnson, A., Smith, J., Wilson, K.",
      venue: "Conference on Neural Information Processing Systems (NeurIPS)",
      links: [
        { label: "PDF", url: "#" },
        { label: "Slides", url: "#" }
      ]
    }
  ];

  const groupedPublications = publications.reduce((acc, pub) => {
    if (!acc[pub.year]) acc[pub.year] = [];
    acc[pub.year].push(pub);
    return acc;
  }, {} as Record<number, typeof publications>);

  return (
    <div className="animate-fade-in">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-4">Publications</h1>
        <p className="text-gray-600 leading-relaxed">
          A selection of my recent research publications in machine learning, natural language processing, 
          and computational linguistics.
        </p>
      </div>

      <div className="space-y-8">
        {Object.entries(groupedPublications)
          .sort(([a], [b]) => parseInt(b) - parseInt(a))
          .map(([year, pubs]) => (
            <section key={year} className="border-l-4 border-blue-200 pl-6">
              <h2 className="text-2xl font-semibold text-gray-800 mb-6">{year}</h2>
              <div className="space-y-6">
                {pubs.map((pub, index) => (
                  <article key={index} className="bg-white p-6 rounded-lg shadow-sm border border-gray-200 hover:shadow-md transition-shadow">
                    <h3 className="text-xl font-semibold text-gray-900 mb-2 leading-tight">
                      {pub.title}
                    </h3>
                    <p className="text-gray-700 mb-2">{pub.authors}</p>
                    <p className="text-gray-600 italic mb-4">{pub.venue}</p>
                    <div className="flex flex-wrap gap-2">
                      {pub.links.map((link, linkIndex) => (
                        <a
                          key={linkIndex}
                          href={link.url}
                          className="inline-flex items-center space-x-1 px-3 py-1 bg-blue-50 text-blue-700 rounded-full text-sm hover:bg-blue-100 transition-colors"
                          onClick={(e) => {
                            e.preventDefault();
                            handleLinkClick(link.url);
                          }}
                        >
                          {link.label === 'PDF' ? (
                            <FileText className="w-4 h-4" />
                          ) : (
                            <ExternalLink className="w-4 h-4" />
                          )}
                          <span>{link.label}</span>
                        </a>
                      ))}
                    </div>
                  </article>
                ))}
              </div>
            </section>
          ))}
      </div>
    </div>
  );
};

const Publications = () => (
  <ErrorBoundary>
    <PublicationsContent />
  </ErrorBoundary>
);

export default Publications;
