
import React, { useState } from 'react';
import { Menu, X } from 'lucide-react';
import ProfileSidebar from '../components/ProfileSidebar';
import Home from '../components/Home';
import Publications from '../components/Publications';
import Talks from '../components/Talks';
import Teaching from '../components/Teaching';
import CV from '../components/CV';
import News from '../components/News';
import Contact from '../components/Contact';

const Index = () => {
  const [activeSection, setActiveSection] = useState('home');
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const renderContent = () => {
    switch (activeSection) {
      case 'home':
        return <Home />;
      case 'publications':
        return <Publications />;
      case 'talks':
        return <Talks />;
      case 'teaching':
        return <Teaching />;
      case 'cv':
        return <CV />;
      case 'news':
        return <News />;
      case 'contact':
        return <Contact />;
      default:
        return <Home />;
    }
  };

  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  const handleLogoClick = () => {
    setActiveSection('home');
    closeMobileMenu();
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Top Navbar */}
      <nav className="fixed top-0 left-0 right-0 bg-white border-b border-gray-200 z-50">
        <div className="max-w-[1500px] mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center">
              <button
                onClick={handleLogoClick}
                className="text-xl font-semibold text-gray-900 hover:text-blue-600 transition-colors cursor-pointer"
              >
                Arjun Dave, PhD
              </button>
            </div>
            {/* Mobile menu button */}
            <div className="lg:hidden">
              <button
                onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                className="text-gray-600 hover:text-blue-600 transition-colors p-2"
              >
                {isMobileMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
              </button>
            </div>
          </div>
        </div>
      </nav>

      {/* Mobile overlay */}
      {isMobileMenuOpen && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
          onClick={closeMobileMenu}
        />
      )}

      {/* Main Content Container */}
      <div className="pt-16 max-w-[1500px] mx-auto">
        <div className="flex">
          {/* Left Sidebar - Profile and Navigation */}
          <ProfileSidebar 
            activeSection={activeSection} 
            setActiveSection={setActiveSection}
            isMobileMenuOpen={isMobileMenuOpen}
            closeMobileMenu={closeMobileMenu}
          />
          
          {/* Right Content Area */}
          <main className="flex-1 lg:ml-80">
            <div className="p-6 lg:p-8">
              {renderContent()}
            </div>
          </main>
        </div>
      </div>

      {/* Footer */}
      <footer className="bg-white border-t border-gray-200 mt-16">
        <div className="max-w-[1500px] mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="text-center text-sm text-gray-600">
            © {new Date().getFullYear()} Academic Portfolio. All rights reserved.
          </div>
        </div>
      </footer>
    </div>
  );
};

export default Index;
